.footer {
    flex-shrink: 0;
    background: #000;
    padding: 0 0 30px 0;
    color: #eee;
    font-size: 14px;
  }
  
  .footer .footer-top {
    background: #111;
    padding: 60px 0 30px 0;
  }
  
  .footer .footer-top .footer-info {
    margin-bottom: 30px;
  }
  
  .footer .footer-top .footer-info h3 {
    font-size: 34px;
    margin: 0 0 20px 0;
    padding: 2px 0 2px 10px;
    line-height: 1;
    font-weight: 700;
    letter-spacing: 3px;
    border-left: 4px solid #f05323;
  }
  
  .footer .footer-top .footer-info p {
    font-size: 14px;
    line-height: 24px;
    margin-bottom: 0;
    color: #eee;
  }
  
  .footer .footer-top .social-links a {
    font-size: 18px;
    display: inline-block;
    background: #333;
    color: #eee;
    line-height: 1;
    padding: 8px 0;
    margin-right: 4px;
    border-radius: 50%;
    text-align: center;
    width: 36px;
    height: 36px;
    transition: 0.3s;
  }
  
  .footer .footer-top .social-links a:hover {
    background: #f05323;
    color: #fff;
  }
  
  .footer .footer-top h4 {
    font-size: 14px;
    font-weight: bold;
    color: #fff;
    text-transform: uppercase;
    position: relative;
    padding-bottom: 12px;
  }
  
  .footer .footer-top h4::before,
  .footer .footer-top h4::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    height: 2px;
  }
  
  .footer .footer-top h4::before {
    right: 0;
    background: #555;
  }
  
  .footer .footer-top h4::after {
    background: #f05323;
    width: 60px;
  }
  
  .footer .footer-top .footer-contact {
    margin-bottom: 30px;
  }
  
  .footer .copyright {
    text-align: center;
    padding-top: 30px;
  }